import axios from "axios";

export const getData = async (url, params = {}) => {
  try {
    const response = await axios.get(url, { params });
    return response?.data;
  } catch (error) {
    handleError(error);
  }
};

export const postData = async (url, data) => {
  try {
    const response = await axios.post(url, data);
    return response?.data;
  } catch (error) {
    handleError(error);
  }
};

export const putData = async (url, data) => {
  try {
    const response = await axios.put(url, data);
    return response?.data;
  } catch (error) {
    handleError(error);
  }
};

export const delData = async (url) => {
  try {
    const response = await axios.delete(url);
    return response?.data;
  } catch (error) {
    handleError(error);
  }
};

const handleError = (error) => {
  if (error.response) {
    switch (error.response.status) {
      case 400:
        console.error("Bad Request: ", error);
        alert("Yu cu khng hp l. Vui lng kim tra li thng tin.");
        // yu cu ngi dng kim tra li thng tin cn thit
        break;
      case 401:
        console.error("Unauthorized: ", error);
        alert("Khng c quyn truy cp. Vui lng ng nhp li.");
        // chuyn hng n trang ng nhp
        break;
      case 404:
        console.error("Not Found: ", error);
        alert("Khng tm thy ti nguyn. Vui lng kim tra li URL.");
        // chuyn hng n trang li 404
        break;
        case 500:
          console.error("Internal Server Error: ", error);
          alert("Li my ch. Th li sau 5 giy.");
          // t ng kt ni li sau 5-10 giy trong 3 ln. nu khng c th bo li cho ngi dng.
          break;
        case 502:
          console.error("Bad Gateway: ", error);
          alert("Cng khng hp l. Th li sau 5 giy.");
          // t ng kt ni li sau 5-10 giy trong 3 ln. nu khng c th bo li cho ngi dng.
          break;
        case 503:
          console.error("Service Unavailable: ", error);
          alert("Dch v tm thi khng kh dng. Th li sau 5 giy.");
          // t ng kt ni li sau 5-10 giy trong 3 ln. nu khng c th bo li cho ngi dng.
          break;
      default:
        console.error("Unexpected error: ", error);
        alert(" xy ra li. Vui lng th li sau.");
    }
  } else {
    console.error("API error: ", error);
    alert("Li mng. Vui lng kim tra kt ni v th li.");
  }
};